/*! FOOD CATEGORIES */

.food_categories {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    width: 80%;
    margin: 15vh auto 0;
    .food_categories-container {
        display: flex;
        flex-direction: column;
        width: 45%;
        h2 {
            text-align: left;
            color: #000;
            font-size: 2.7rem;
            font-weight: 700;
            line-height: calc(2.7rem * 1.5);
            user-select: none;
            &.dark__mode {
                color: #fff;
            }
        }
        p {
            font-size: 1.05rem;
            font-weight: 500;
            margin: 0.5rem 0 1.25rem;
            text-align: left;
            line-height: calc(1.05rem * 1.25);
        }
        button {
            background-color: #000;
            color: #fff;
            font-size: 1.05rem;
            line-height: calc(1.05rem * 1.25);
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 8px;
            padding: 12px;
            width: 160px;
            height: 48px;
            outline: none;
            border: 1px solid #000;
            font-weight: 500;
            cursor: pointer;
            user-select: none;
            &.dark__mode {
                background-color: #4361EE;
                color: #fff;
            }
        }
    }
    .categories_list {
        margin-top: 1.5rem;
        width: 95%;
        &:first-of-type {
            margin-top: 2.75rem;
        }
        .category_list-item {
            display: flex;
            justify-content: space-between;
            align-items: center;
            border-bottom: 2px solid #bfbfbf;
            padding-bottom: 0.75rem;
            img {
                margin-top: 0.2rem;
                margin-left: 0.25rem;
                height: 45px;
                width: 45px;
                user-select: none;
            }
            .category_list-info {
                width: 57.5%;
                .category-name {
                    font-size: 1.25rem;
                    color: #000;
                    margin: 0;
                }
                .category-description {
                    color: rgba(0, 0, 0, 0.50);
                    font-size: 15.5px;
                    margin: 0.5rem 0 0;
                    user-select: none;
                }
            }
            .item-number {
                font-weight: 600;
                font-size: 1.15rem;
                color: #000;
                width: 20%;
                text-align: right;
            }
        }
    }
}