/*! DATA CHARTS */

.data_charts {
    color: #000;
    margin-top: 15vh;
    h2 {
        font-size: 2.8rem;
        line-height: calc(2.7rem * 1.5);
    }
    .data_charts-info {
        font-size: 1.25rem;
        line-height: calc(1.25rem * 1.25);
        margin-top: 1.25rem;
        user-select: none;
        &.dark__mode {
            color: #d4d4d4 !important;
        }
    }
    .products_stats {
        display: flex;
        width: 70%;
        justify-content: space-between;
        align-items: center;
        margin: 2.5rem auto 5rem;
        .single-info-wrapper {
            padding: 1.25rem 1.5rem;
            text-align: left;
            width: 30%;
            border-radius: 6px;
            border: 1px solid rgba(0, 0, 0, 0.3);
            p {
                font-size: 1.3rem;
                font-weight: 500;
                color: rgba(0, 0, 0, 0.55);
                display: flex;
            }
            span {
                font-size: 2.3rem;
                font-weight: 700;
                display: flex;
                margin: 0.75rem 0 0;
                &.dark__mode {
                    color: #80c423 !important;
                }
            }
            &.dark__mode {
                border-color: #3a0ca3;
                border-width: 2px;
            }
        }
    }
    .radial_chart-pie {
        width: 80%;
        margin: 0 auto;
        h2 {
            font-size: 2rem;
        }
    }
}

.TESTING {
    color: red !important;
    g rect {
        color: red !important;
    }
}