/*! HEADER */

header {
    padding: 1.25rem 0;
    position: fixed;
    width: 100%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgb(30, 30, 30);
    top: 0;
    z-index: 9999;
    &.night-mode {
        background-color: #000;
    }
    .toggle-night-mode {
        display: flex;
        justify-content: center;
        align-items: center;
        line-height: 1;
        #slider {
            font-size: 15px;
            appearance: none;
            width: 60px;
            height: 30px;
            background-color: #e5e5e5;
            border-radius: 3em;
            position: relative;
            cursor: pointer;
            outline: none;
            transition: all .2s ease-in-out;
            border: 2px solid #fff;
            &:after {
                position: absolute;
                content: "";
                width: 30px;
                height: 30px;
                border-radius: 50%;
                background-color: #222;
                box-shadow: 0 0 0.25em rgba(0, 0, 0, .3);
                transform: scale(.7);
                top: -2px;
                left: 0;
                transition: all .2s ease-in-out;
            }
            &:checked {
                width: 60px;
                height: 30px;
                background-color: rgb(20, 20, 20);
                transition: all .2s ease-in-out;
            }
        }
    }
    h1 {
        font-family: "Lexend", sans-serif;
        display: flex;
        align-items: center;
        font-size: 2.2rem;
        letter-spacing: 3px;
        word-spacing: 5px;
        color: #fff;
        text-transform: uppercase;
        &.night-mode {
            color: #fff;
        }
    }
    .header-button {
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000;
        font-size: 1.05rem;
        font-weight: 600;
        word-spacing: 1.75px;
        letter-spacing: 0.1px;
        line-height: calc(1.05rem * 1.22);
        border-radius: 8px;
        padding: 0.65rem 1.25rem;
        outline: none;
        border: none;
        cursor: pointer;
        user-select: none;
        transition: .165s ease-in-out;
        a {
            font-family: "Lexend", sans-serif;
            color: #000;
            text-decoration: none;
            transition: .165s ease-in-out;
        }
        &:hover {
            background-color: rgb(85, 85, 85);
            a {
                color: #ffffff;
            }
        }
    }
}

.toggle-night-mode #slider:checked:after {
    left: calc(100% - 2.075em);
    background-color: #e5e5e5;
}

.dark__mode {
    color: #fff !important;
}