/* ! ADD PRODUCT */

.add_product {
    margin: 10vh auto;
    width: 80%;
    height: 100vh;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    user-select: none;
    position: relative;
    .added-modal {
        z-index: 10001;
        font-family: 'Roboto';
        line-height: calc(1.125rem * 1.3);
        font-size: 1.05rem;
        letter-spacing: 0.15px;
        word-spacing: 2px;
        position: fixed;
        right: 2.75rem;
        bottom: 2.5rem;
        background-color: rgb(235, 235, 235);
        color: #000;
        border-radius: 12px;
        padding: 0.825rem 1.8rem;
        border: 1px solid black;
        display: flex;
        justify-content: center;
        align-items: center;
        opacity: 0;
        transition: .25s ease-in-out;
        .icon {
            background-color: black;
            color: #fff;
            border-radius: 50%;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.15rem;
            margin-right: 0.4rem;
        }
        &.modal-active {
            opacity: 1;
        }
    }
    .add_product-image {
        object-fit: contain;
        object-position: center;
        width: 40%;
        height: 75%;
        user-select: none;
    }
    .add_product-container {
        text-align: left;
        width: 50%;
        h2 {
            color: #000;
            font-size: 3.2rem;
            font-weight: 700;
            text-align: left;
            letter-spacing: -0.75px;
            word-spacing: 4px;
            margin-bottom: 3rem;
            line-height: calc(3rem * 1.5);
            &.dark__mode {
                color: #fff;
            }
        }
        label {
            color: #000;
            font-weight: 500;
            font-size: 1.3rem;
            line-height: calc(1.3rem * 1.3);
            input {
                display: block;
                margin-bottom: 1.5rem;
                margin-top: 0.25rem;
                width: 60%;
                text-align: left;
                padding: 9px 12px;
                font-size: 1rem;
                border-radius: 6px;
                border: 1px solid #707070;
            }
        }
        .products_categories {
            margin-top: 0.1rem;
            display: flex;
            flex-direction: row;
            justify-content: flex-start;
            max-width: 90%;
            flex-wrap: wrap;
            .product-category-selector {
                font-weight: 400;
                font-size: 1.125rem;
                line-height: calc(1.125rem * 1.25);
                background: rgba(0, 0, 0, 0.125);
                padding: 8px 10px;
                margin-right: 6px;
                margin-top: 6px;
                border-radius: 6px;
                cursor: pointer;
                transition: 0.125s ease-in-out;
                &.dark__mode {
                    font-weight: 500;
                    color: #000 !important;
                    background: rgb(255, 255, 255);
                }
            }
            .product-category-selector.category-selected {
                background: rgba(0, 0, 0, 0.95);
                color: #fff;
                transition: 0;
                &.dark__mode {
                    color: #fff !important;
                    background-color: #4361EE;
                }
            }
        }
        .form__buttons {
            margin-top: 1rem;
            display: flex;
            flex-direction: row;
            .form__cancel,
            .add {
                line-height: 24px;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 8px;
                padding: 12px;
                width: 160px;
                height: 48px;
                outline: none;
                border: 1px solid #000;
                font-size: 1rem;
                font-weight: 600;
                cursor: pointer;
            }
            .form__cancel {
                background-color: #fff;
                &.dark__mode {
                    color: #000 !important;
                }
            }
            .add {
                background-color: #000;
                color: #fff;
                margin-left: 12px;
                &.dark__mode {
                    background-color: #3C096C;
                }
            }
        }
    }
}

.input-container {
    position: relative;
    margin: 5rem 0 2rem;
    width: 60%;
    input[type="text"] {
        font-size: 20px;
        line-height: calc(20px * 1.25);
        width: 100%;
        border: none;
        border-bottom: 2px solid #ccc;
        padding: 5px;
        background-color: transparent;
        outline: none;
        text-align: left;
        &.dark__mode {
            color: #fff;
        }
    }
    .label {
        position: absolute;
        top: 0;
        left: 0;
        color: #000;
        transition: all 0.3s ease;
        pointer-events: none;
    }
    .underline {
        position: absolute;
        bottom: 0;
        left: 0;
        height: 2px;
        width: 100%;
        background-color: #333;
        transform: scaleX(0);
        transition: all 0.3s ease;
        &.dark__mode {
            background-color: #666;
        }
    }
}

.input-container input[type="text"]:focus~.label,
.input-container input[type="text"]:valid~.label {
    top: -20px;
    font-size: 16px;
    color: #333;
}

.input-container input[type="text"]:focus~.underline,
.input-container input[type="text"]:valid~.underline {
    transform: scaleX(1);
}